:root {
  --pf-global--FontSize--md: var(--pf-global--FontSize--sm);
  --pf-global--link--Color: #0088ce;
  --pf-c-nav__list-link--m-current--after--BackgroundColor: #0088ce;
  --pf-global--link--Color--hover: #00659c;
  --pf-c-backdrop--Color: rgba(3,3,3,.62);
}

.pf-c-page__header-brand-link .pf-c-brand {
  --pf-c-page__header-brand-link--c-brand--MaxHeight: 40px;
  height: 40px;
}

.pf-c-dropdown__toggle {
  --pf-c-dropdown__toggle--FontSize: var(--pf-global--FontSize--sm);
}

.pf-c-dropdown__toggle.syn-help-dropdown {
  --pf-c-dropdown__toggle--FontSize: var(--pf-global--FontSize--md);
}

.pf-c-page {
  width: 100%;
  height: 100vh;
}

.pf-c-dropdown__menu-item {
  --pf-c-dropdown__menu-item--FontSize: var(--pf-global--FontSize--xs);
}

body {
  line-height: var(--pf-global--LineHeight--sm);
  color: var(--pf-global--Color--100);
}

#root {
  flex: 1;
  display: flex;
  height: 100%;
}

.navbar-pf .navbar-utility > li > button {
  padding: 9px 10px;
}

.blank-slate-pf .blank-slate-pf-secondary-action a {
  padding: 2px 6px;
  margin-bottom: 0;
}

.modal-body > div {
  width: 100%;
}

.list-view-pf-body .list-group-item-heading {
  font-size: var(--pf-global--FontSize--sm);
}

.label {
  font-size: var(--pf-global--FontSize--sm);
}

.syn-scrollable {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.syn-scrollable--body {
  flex: 1 1 auto;
  overflow: auto;
}

.pf-c-backdrop {
  background-color: var(--pf-c-backdrop--Color);
}

.pf-c-about-modal-box__content dd {
  word-break: break-word;
}

.pf-c-content {
  --pf-c-content--FontSize: var(--pf-global--FontSize--sm);
  --pf-c-content--dt--FontWeight: bold; /* override for OpenSans not supporting weight 500 */
}

.list-view-pf-view {
  margin-top: 0;
}

.syn-truncate__ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.pf-c-about-modal-box__hero {
  background-position: right;
}

.pf-c-clipboard-copy {
  display: inline-block;
  width: 455px;
  max-width: 100%;
}

@media (max-width: 768px) {
  .pf-c-clipboard-copy {
    display: inline-block;
  }

  .pf-c-clipboard-copy__group-copy {
    --pf-c-clipboard-copy__group-copy--PaddingLeft: 16px;
    --pf-c-clipboard-copy__group-copy--PaddingRight: 16px;
  }
}

.customization-details__heading {
  background-color: #f5f5f5;
  font-weight: bold;
  margin-bottom: 8px;
  margin-top: 8px;
  padding: 4px 8px;
}

/* Wizard overrides */
.wizard-pf-steps-indicator,
.wizard-pf-steps {
  background: #fff;
}

.wizard-pf-step-alt .wizard-pf-step-alt-number {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
}

@media (min-width: 768px) {
  .wizard-pf-steps-indicator {
    height: auto;
    padding-bottom: calc(1em + 25px);
  }
}

@media (min-width: 768px) {
  .wizard-pf-steps-indicator .wizard-pf-step .wizard-pf-step-number {
    top: 100%;
    left: 50%;
    -webkit-transform: translate(-50%, .5em);
            transform: translate(-50%, .5em);
  }
}

.wizard-pf-steps-indicator .wizard-pf-step:before {
  top: calc(100% + .5em + calc(25px / 2));
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}

.wizard-pf-step-title {
  line-height: 24px;
}

.list-view-pf .list-group-item-heading {
  white-space: normal;
  word-break: break-word;
}

.connection-card,
.connection-card:hover {
  color: inherit;
  cursor: pointer;
  height: 240px;
  position: relative;
  text-decoration: none;
  padding-top: 2em;
}

.connection-card:hover {
  box-shadow: 0 1px 6px rgba(3,3,3,.35);
}

.connection-card__tech-preview {
  color: #363636;
  cursor: pointer;
  font-size: small;
  padding: 5px 10px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  text-align: right;
  border-bottom: 1px solid #d1d1d1;
}

.connection-card__heading--no-border {
  border-bottom: 1px solid #FFFF;
  font-size: small;
  padding: 4px;
}

.connection-card__dropdown {
  position: absolute;
  top: calc(2em + 8px);
  right: 10px;
}

a.connection-card__content:hover,
a.connection-card__content:focus {
  color: initial;
  text-decoration: none;
}

.connection-card__body {
  display: flex;
  align-items: center;
  flex-flow: column;
  text-align: center;
}

.connection-card__body .connection-card__icon {
  height: 35px;
  width: 35px;
  margin-bottom: 25px;
  padding: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.connection-card__body .connection-card__title,
.connection-card__body .connection-card__title:hover {
  color: initial;
  text-decoration: none;
}

.connection-card__body .connection-card__description {
  color: initial;
  font-size: small;
  font-weight: 300;
  margin-top: 15px;
  text-decoration: none;
}

.connection-card__footer--config-required {
  background-color: #fdf2e5;
  border-color: #ec7a08;
  font-size: smaller;
  text-align: center;
  padding: 15px;
}

.connection-details-form__buttonBar {
  border-top: none;
  margin-left: auto;
  padding: 0px 20px 10px;
  width: 90%;
}

.connection-details-form__editButton {
  margin-bottom: 8px;
  margin-right: 4px;
}

.connection-details-form__formFields {
  margin-left: auto;
  width: 90%;
}

.connection-details-header__row {
  align-items: center;
}

.connection-details-header__connectionName {
  font-size: 2em;
}

.connection-details-header__connectionIcon {
  width: 46px;
}

.dashboard__connections__actions .view,
.dashboard__integrations__actions .view {
  padding: 0.3em 0.8em;
}

.dashboard__connections__actions {
  padding: 0 10px;
}

.dashboard__metrics .card-pf.card-pf-aggregate-status {
  padding: 0 10px 24px 10px;
}

.dashboard__metrics .card-pf-title .card-pf-aggregate-status-count {
