}

.Loader.is-block {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%;
}

.Loader.is-inline {
  display: inline-flex;
  align-items: center;
}

.metrics-uptime__header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.metrics-uptime__uptime {
  font-size: 11px;
  color: #8b8d8f;
}

 .wizard-pf-steps-alt-indicator:after {
  pointer-events: none;
  cursor: pointer;
}


.conditions-dropdown-item__condition[data-verb=WHEN] {
  color: #3C96D4;
}
.conditions-dropdown-item__condition[data-verb=OTHERWISE] {
  color: #61AF5A;
}

.integration-flow-add-step {
  display: flex;
  z-index: 2;
  position: relative;
  justify-content: center;
  margin: 5px 0;
}

.integration-flow-add-step__iconWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background-color: #158acc;
  font-size: 18px;
  width: 24px;
  height: 24px;
  border-radius: 5px;
}

.integration-flow-add-step__iconWrapper .fa {
  color: #fff;
}

.integration-flow-add-step__iconWrapper::before {
  content: '';
  position: absolute;
  width: 2px;
  background-color: #d4d4d4;
  top: -5px;
  bottom: -5px;
  left: calc(100% / 2);
  z-index: -1;
}

.integration-flow-add-step__links .popover-content {
  display: flex;
  flex-flow: column;
}

.integration-flow-add-step__links .popover-content a {
  margin: 3px 0;
}

.conditions-back-button-item__back-button-wrapper {
  text-align: center;
  padding: 0 8px;
}
.conditions-back-button-item__back-button-wrapper .btn {
  width: 100%;
}

.integration-flow-step {
  display: flex;
  margin: 20px 0;
}
.integration-flow-step:last-child {
  align-items: flex-start; /* to cut the vertical line at the end of the icon, and not of the whole container */
}

.integration-flow-step__iconWrapper {
  display: flex;
  justify-content: center;
  position: relative;
  flex: 0 0 100px;
  z-index: 1;
}

.integration-flow-step:not(:first-child) .integration-flow-step__iconWrapper::before {
  content: '';
  position: absolute;
  width: 2px;
  background-color: #d4d4d4;
  top: -20px; /* same as root container top margin */
  bottom: 0;
  left: calc(100% / 2);
  z-index: 0;
}

.integration-flow-step:not(:last-child) .integration-flow-step__iconWrapper::after {
  content: '';
  position: absolute;
  width: 2px;
  background-color: #d4d4d4;
  top: 0;
  bottom: -20px; /* same as root container bottom margin */
  left: calc(100% / 2);
  z-index: 0;
}

.integration-flow-step__icon {
  border: 3px solid #d0d0d0;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  width: 55px;
  height: 55px;
  position: relative;
  z-index: 1;
  color: #d0d0d0;
}


.is-active .integration-flow-step__icon {
  border-color: #0088ce;
}
.integration-flow-step-details {
  width: 100%;
  margin-top: 15px;
  margin-bottom: 40px;
}

.integration-flow-step-details__title {
  background-color: #d1d1d1;
  padding: 2px 20px;
}

.integration-flow-step-details.is-active .integration-flow-step-details__title {
  background-color: #158acc;
  color: #fff;
}

.integration-flow-step-details__body {
  padding: 10px 20px;
}

.integration-flow-step-details.is-active .integration-flow-step-details__body {
  background-color: #d6eefa;
}

.integration-flow-step-details__overview {
  display: flex;
  margin: 0;
}

.integration-flow-step-details__overview dt {
