.views-import-layout {
  flex: 1 0;
  display: flex;
  flex-flow: column;
  overflow-y: auto;
  overflow-x: hidden;
}

.views-import-layout__body {
  display: flex;
  flex: 1 0;
}

.views-import-layout__contentOuter {
  display: flex;
  flex-flow: column;
}

.views-import-layout__sectionLight,
/* hack to make the toolbar match the header */
.views-import-layout__contentOuter .toolbar-pf {
  background-color: var(--pf-global--BackgroundColor--200);
}

@media (min-width: 768px) {
  .views-import-layout {
    height: 100%;
    display: flex;
    flex-flow: column;
  }

  .views-import-layout__header {
    border-bottom: 1px solid #ededed;
  }

  .views-import-layout__body {
    flex: 1 0;
    display: flex;
    width: 100%;
    overflow: hidden;
  }

  .views-import-layout__contentOuter {
    overflow: auto;
    padding: 0;
    width: 100%;
  }

  .views-import-layout__contentInner {
    flex: 1;
    display: flex;
    flex-flow: column;
  }
}

.api-connector-card {
  color: inherit;
  display: flex;
  justify-content: center;
  text-align: center;
  text-decoration: none;
}

.api-connector-card__icon {
  max-height: 50px;
  max-width: 50px;
}

.api-connector__title {
  font-weight: 400;
}

.api-connector-details-form__body {
  margin-top: 20px;
}

.api-connector-details-form__card {
  margin-bottom: 0;
}

.api-connector-details-form__buttonBar {
  border-top: none;
  margin-left: auto;
  padding: 0px 20px 10px;
  width: 90%;
}

.api-connector-details-form__editButton {
  margin-bottom: 8px !important;
  margin-right: 4px;
}

.api-connector-details-form__formFields {
  margin-left: auto;
  width: 90%;
}

.api-connector-details-form__icon {
  width: 50px;
  line-height: 50px;
  font-size: 40px;
  vertical-align: middle;
  text-align: center;
  margin-right: 20px;
  display: inline-block;
  background: #d1d1d1;
  color: #72767b;
  border: 1px solid #72767b;
}

.api-connector-details-form__iconContainer {
  margin-bottom: 20px;
}

.api-connector-details-form__title {
  font-size: 1.25em;
}

.api-connector-details-form__iconContainer input[type="file"] {
  display: inline-block;
}

.api-connector-review__heading {
  background-color: #f5f5f5;
  font-weight: bold;
  margin-bottom: 8px;
  margin-top: 8px;
  padding: 4px 8px;
}

.api-connector-review__tagMessageList {
  --pf-c-content--ul--ListStyle: none;
}

.api-connector-review__validationFallbackMessage {
  font-weight: bold;
}
.extension-detail__extensionId {
  font-size: 12px;
  margin-left: 10px;
}

.extension-detail__extensionTitle {
  --pf-c-content--h1--MarginBottom: 0;
}

.extension-detail__extensionId, .extension-detail__extensionTitle {
  display: inline-block;
}

.extension-detail__titleButtons .btn + button {
  margin-left: 4px;
}

.extension-import-review__cancelButton {
  margin-left: 4px;
}

.extension-import-review .extension-import-review__propertyLabel {
  text-align: right;
  font-weight: bold;
}

.extension-import-review .extension-import-review__actions-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.extension-overview__propertyLabel {
  font-weight: bold;
  text-align: right;
}

.extension-overview__propertyValue {
  text-align: left;
}

.integration-detail-activity-toolbar {
  margin-bottom: 10px;
}

.integration-detail-activity-toolbar .integration-detail-activity-toolbar-last-refresh {
  color: #8b8d8f;
}

.integration-detail-activity-item {
  border: none;
}

.integration-detail-activity-item .list-view-pf-main-info {
  min-height: 68px;
}

.integration-detail-activity-item .integration-detail-activity-item__status-item {
  padding-top: 6px;
}

.integration-detail-activity-item .integration-detail-activity-item__output {
  white-space: nowrap;
}

.integration-detail-activity-item .integration-detail-activity-item__output {
  padding: 0;
  vertical-align: middle;
}

.integration-detail-activity-item .table-hover tr:hover .integration-detail-activity-item__output,
.integration-detail-activity-item .integration-detail-activity-item__output {
  background: #fafafa;
}

.integration-detail-activity-item__output-step-data {
  max-height: 15em;
  padding: 5px;
  border: 0;
  white-space: pre-wrap;
  word-wrap: break-word;
}

.integration-detail-activity-item__expanded-table {
  margin-top: 25px;
}

.view-integration-btn + .integration-actions__dropdown-kebab {
  margin-left: 20px;
}

.integration-detail-editable-name {
  margin-right: 15px;
}

.integration-detail-history-list-view {
    background-color: white;
}

.integration-detail-history-list-view__description {
    font-weight: bold;
    padding-top: 20px;
}

.integration-detail-history-list-view .list-view-pf-view {
    margin-top: 0;
}

.integration-detail-history-list-view .list-view-pf-left .list-view-pf-icon-sm {
  border: none;
  font-size: 0.8em;
}

.integration-detail-history-list-view__draft-row .list-group-item-heading {
    margin-left: 50px;
}

.integration-detail-history-list-view__draft-row .integration-detail-history-list-view__description {
    padding-top: 30px;
}

.integration-detail__breadcrumb {
  background-color: #FFFFFF;
}

.integration-detail-info {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.integration-detail-info__status {
  white-space: nowrap;
}

.integration-detail-info__status-icon {
  margin-right: 5px;
}

.integration-editor-steps-list-item__list-item.list-group-item.list-view-pf-expand-active {
  background: #fff;
  border: 1px solid #f5f5f5;
  box-shadow: none;
}
.integration-editor-steps-list-item__list-item.list-group-item.list-view-pf-expand-active:hover {
  background: #edf8ff;
}
.integration-editor-steps-list-item__list-item .list-group-item-container {
  border-top: solid 1px #d1d1d1;
}
.integration-editor-steps-list-item__list-item .list-view-pf-expand .fa {
  font-size: 180%;
  margin-right: 16px;
  color: #151515;
}

.integration-editor-layout {
  flex: 1 0;
  display: flex;
  flex-flow: column;
  overflow-y: auto;
  overflow-x: hidden;
}

.integration-editor-layout__sidebarOuter {
  display: none;
}

.integration-editor-layout__body {
  display: flex;
  flex: 1 0;
  width: 100%;
}

.integration-editor-layout__contentOuter {
  display: flex;
  flex-flow: column;
  flex-grow: 1;
}

.integration-editor-layout__contentInner {
  display: flex;
  flex-flow: column;
  flex-grow: 1;
}

.integration-editor-layout__sectionLight,
/* hack to make the connections toolbar match the header */
.integration-editor-layout__contentOuter .toolbar-pf {
  background-color: var(--pf-global--BackgroundColor--200);
}

@media (min-width: 768px) {
  .integration-editor-layout {
    height: 100%;
    display: flex;
    flex-flow: column;
  }

  .integration-editor-layout__sidebarOuter {
    background-color: var(--pf-global--BackgroundColor--200);
    box-shadow: rgba(3, 3, 3, 0.07) 12px 0 10px -4px;
    flex: 0 0 auto;
    display: flex;
    overflow: hidden;
    z-index: 1;
  }
  .integration-editor-layout__sidebarInner {
    background-color: var(--pf-global--BackgroundColor--200);
    display: flex;
  }

  .integration-editor-layout__header {
    border-bottom: 1px solid #ededed;
  }

  .integration-editor-layout__body {
    overflow: hidden;
  }

  .integration-editor-layout__contentOuter {
    overflow: auto;
    padding: 0;
    width: 100%;
  }

  .integration-editor-layout__contentInner {
    display: flex;
    flex-flow: column;
  }
}



.integration-editor-steps-list {
  background: transparent;
  max-width: 900px;
  margin: auto;
}

.integration-editor-wizard {
  height: calc(100% - 36px - 60px);
}

.integration-editor-wizard__body {
  max-height: none;
}

.integration-editor-wizard.has-footer .integration-editor-wizard__body {
  height: calc(100vh - 60px - 36px - 57px - 61px);
  max-height: none;
}

@media (min-width: 768px) {
  .integration-editor-wizard__body {
    height: calc(100vh - 121px - 60px - 36px);
    max-height: none;
  }

  .integration-editor-wizard.has-footer .integration-editor-wizard__body {
    height: calc(100vh - 121px - 60px - 36px - 61px);
    max-height: none;
  }
}

.integration-editor-wizard__contentWrapper {
  overflow: auto;
  padding: 0;
}

.integration-editor-wizard__content {
  max-width: 900px;
  margin: auto;
}

.integration-editor-wizard__footer {
  padding: 17px 15px;
  z-index: 1;
}

.integration-detail-metrics .card-pf.card-pf-aggregate-status {
  padding-top: 10px;
  padding-bottom: 10px;
}

.integration-detail-metrics__uptime-header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}

.integration-detail-metrics__uptime-uptime {
  font-size: 11px;
  color: #8b8d8f;
}

@import url('~codemirror/addon/lint/lint.css');
@import url('~codemirror/lib/codemirror.css');

.text-editor .cm-mustache {
  color: #b21eab;
  font-weight: bold;
}

.text-editor .cm-freemarker {
  color: #36832e;
  font-weight: bold;
}

.CodeMirror-hints {
  z-index: 999;
}

@media (min-width: 992px) {
  .api-provider-review-operations-item .list-view-pf-actions {
    min-width: 20%;
    text-align: right;
  }
}

